@import "../../scss/App.scss";

.composition {
  position: relative;

  &__photo {
    box-shadow: 0 1.5rem 4rem rgba($color-black, .4);
    border-radius: 2px;
    outline-offset: 2rem;
    position: absolute;
    transition: all .2s;
    width: 55%;
    z-index: 10;

    @include respond(tab-port) {
      box-shadow: 0 1.5rem 3rem rbga($color-black, .2);
      float: left;
      position: relative;
      width: 33.3333%;
    }

    &--p1 {
      left: 0;
      top: -2rem;

      @include respond(tab-port) {
        top: 0;
        transform: scale(1.2);
      }
    }
    &--p2 {
      right: 0;
      top: 2rem;

      @include respond(tab-port) {
        top: -1rem;
        transform: scale(1.3);
        z-index: 100;
      }
    }
    &--p3 {
      left: 20%;
      top: 10rem;

      @include respond(tab-port) {
        left: 0;
        top: 1rem;
        transform: scale(1.1);
      }
    }

    &:hover {
      box-shadow: 0 2.5rem 4rem rbga($color-black, .5);
      outline: 1.4rem solid $color-primary;
      transform: scale(1.05) translateY(-.5rem);
      z-index: 20;
    }
  }

  &:hover &__photo:not(:hover) {
    transform: scale(.95);
  }
}

.section-about {
  background-color: $color-grey-light-1;
  margin-top: 25rem 0;
  padding: 5rem;

  @include respond(tab-port) {
    padding: 20rem 0;
  }
}